ógico existente en una empresa en un momento determinado, sino también el esfuerzo de un competidor en investigación y desarrollo (I+D), la aparición de nuevas industrias y la canibalización de una empresa a través de productos más nuevos. Lamentablemente, la naturaleza multifacética de la obsolescencia tecnológica se ha medido indirectamente en la literatura a través de las citas de patentes.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-botones",attrs:{"id":"t_6_3"}},[_c('h2',[_vm._v("6.3 Planes")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center"},[_c('p',[_vm._v("El proceso de programación de un plan de mantenimiento puede dividirse en ocho pasos manejables:")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5"},[_c('div',{staticClass:"cajon cajon-tema6 p-4",attrs:{"data-aos":"flip-up"}},[_c('p',[_vm._v("Es importante que cuando haya que hacer cambios en el calendario, se aseguren de llevar a cabo y se comuniquen de acuerdo con el plan establecido en el Paso 1. A lo largo del proyecto, se asegurará de que cada actividad se ajuste al calendario y determinará si es necesario adoptar medidas correctivas en caso de que se produzcan retrasos.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo color-acento-botones",attrs:{"id":"t_6_4"}},[_c('h2',[_vm._v("6.4 Formatos")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bloque-texto-g__texto p-4",attrs:{"data-aos":"flip-down"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Los formatos de la documentación técnica se refieren a cualquier documento que explique el uso, la funcion